@import '../../../common/styles/variable';

.skill {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px 20px;
    background-color: $bgColorFirst;
    border-radius: 40px;
    list-style-type: none;
    border: 2px solid $borderColor;
    transition: all 0.2s ease-in;
    user-select: none;

    &:hover {
        box-shadow: 2px 2px 5px #000;
        transform: scale(1.1);
        transition: all 0.15s ease-out;
        & svg {
            transition: all 0.15s;
            fill: $bgColorThird;
        }
    }

    & svg {
        height: 30px;
        width: 30px;
        transition: all 0.3s;
        fill: $mainColor;
    }

    .title {
        color: $titleColor;
    }
}
