@import '../../common/styles/variable';

.mainBlock {
    min-height: 100vh;
    background-color: $bgColorFirst;
    display: flex;
    align-items: center;

    .wrapper {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        justify-content: center;
        gap: 90px;

        .icon {
            fill: $iconColor;
            width: 17px;
            height: 15px;
            margin-left: 5px;
        }

        @media (max-width: 940px) {
            @media (max-width: 800px) {
                justify-content: center;
                gap: 50px;
            }
        }

        .circle {
            border: 12px solid $bgColorSecond;
            border-radius: 50%;
            aspect-ratio: 1;
            user-select: none;

            //@media (max-width: 550px) {
            //  width: 80%;
            //  height: 80vw;
            //}

            .wrapperPhoto {
                width: clamp(150px, 80vw, 360px);
                height: clamp(150px, 80vw, 360px);
                background-color: $bgColorThird;
                border-radius: 50%;
                border: 12px solid #19283b;
                position: relative;

                .photo {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
        }

        .wrapperDescription {
            display: flex;
            flex-direction: column;
            width: 264px;
            @media (max-width: 800px) {
                align-items: center;
                width: 240px;
            }

            .greetings {
                font-size: 18px;
                color: $titleColor;
                @media (max-width: 700px) {
                    font-size: 16px;
                }
            }

            .wrapperTitle {
                display: flex;
                align-items: center;
                gap: 20px;

                .title {
                    color: $titleColor;
                    font-size: 50px;
                    padding: 10px 0;
                    @media (max-width: 700px) {
                        font-size: 35px;
                    }
                }

                .logoReact {
                    height: 60px;
                    width: 60px;
                    fill: $mainColor;
                    animation: 12s linear infinite rotate;
                    .download {
                        margin-left: 7px;
                    }
                }
            }

            .subTitle {
                font-size: 22px;
                color: $titleColor;
                margin-bottom: 30px;
                @media (max-width: 800px) {
                    font-size: 20px;
                }
            }

            .button {
                display: inline-flex;
                align-items: center;
                justify-content: center;
                padding: 15px 35px;
                font-size: 18px;
                font-weight: 700;
                border-radius: 10px;
                color: $iconColor;
                text-decoration: none;
                border-color: $mainColor;
                box-shadow:
                    0 0 15px 35px $mainColor inset,
                    0 0 0 0 $mainColor;
                transition: all 150ms ease-in-out;
                &:hover {
                    box-shadow:
                        0 0 10px 0 $mainColor inset,
                        0 0 10px 4px $mainColor;
                }
            }
        }
    }
}

@keyframes rotate {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}
